.root{margin-bottom:20px}.root .title{margin:0}.root .subtitle{font-family:"Inter",Roboto,sans-serif;font-weight:normal;line-height:1;letter-spacing:.01em;color:#344562;font-size:12pt;color:#868da1;margin-bottom:32px}.root .row{display:flex;flex-direction:row;gap:24px;margin-bottom:24px}.root .row+hr{margin:0 0 24px 0}.root .key{width:100%}.root .key input{width:100%}.root .key .checkWrapper{position:relative}.root .key .checkWrapper:after{position:absolute;top:0;right:0;padding-right:18px;height:100%;display:flex;align-items:center;color:#16a63e;content:"✓";font-size:26px}.root .ghost{font-family:"Inter",Roboto,sans-serif;font-weight:normal;line-height:1;letter-spacing:.01em;color:#344562;font-family:"Inter-SemiBold",Roboto,sans-serif;font-size:14pt;padding:17px 24px;border-radius:8px;border:1px solid #efeff8;cursor:pointer;background-color:#fff;color:#1a88e8}.root .ghost:disabled{color:rgba(26,136,232,.1);cursor:not-allowed}.root .ghostSecondary{font-family:"Inter",Roboto,sans-serif;font-weight:normal;line-height:1;letter-spacing:.01em;color:#344562;font-family:"Inter-SemiBold",Roboto,sans-serif;font-size:14pt;padding:17px 24px;border-radius:8px;border:1px solid #efeff8;cursor:pointer;background-color:#fff;color:#1a88e8;color:#868da1}.root .ghostSecondary:disabled{color:rgba(26,136,232,.1);cursor:not-allowed}.root .ghostSecondary:disabled{color:#babcd1;cursor:not-allowed}.root .copied{color:#16a63e}